 увеличить срок службы и оптимизировать использование энергии и материальных ресурсов. Компания последовательно стремится обеспечить снижение расхода ресурсов как у себя, так и у своих партнеров, которые доверяют продукции Sika. Инициативы и действия компании Sika соответствуют требованиям Глобальной инициативы по отчетности.")])])])]),_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-textimage__list-bullet"},[_c('div',{staticClass:"cmp-image--right cmp-image"},[_c('picture',{attrs:{"data-image-src":"{\"isResponsive\":true,\"pattern\":\"https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ${imageProfile}?fit\\u003dcrop%2C1\\u0026wid\\u003d${width}\\u0026hei\\u003d${height}\\u0026fmt\\u003d${format}\",\"originalWidth\":3800,\"originalHeight\":2533,\"alt\":\"Kids at Project Inspire Tanzania\",\"imageProfileNamePrefix\":\":\",\"imageProfileMap\":{}}","data-height-based":"false"}},[_c('source',{attrs:{"type":"image/webp","srcset":"https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=480&hei=320&fmt=webp-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=600&hei=400&fmt=webp-alpha" + " 1.5x, " + "https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=768&hei=512&fmt=webp-alpha" + " 2x"}}),_c('source',{attrs:{"type":"image/png","srcset":"https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=480&hei=320&fmt=png-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=600&hei=400&fmt=png-alpha" + " 1.5x, " + "https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=768&hei=512&fmt=png-alpha" + " 2x"}}),_c('img',{staticClass:"cmp-image__image",attrs:{"src":"https://sika.scene7.com/is/image/sika/glo-project-inspire-TZ?fit=crop%2C1&wid=480&hei=320&fmt=","alt":"Kids at Project Inspire Tanzania"}})])]),_c('p',[_vm._v("При этом во главу угла компания Sika ставит вопросы безопасности. Благополучие и здоровье своих сотрудников и партнеров являются необходимым условием для движения вперед компании Sika. Создание безопасных условий труда всегда было и остается первоочередной задачей.")]),_c('p',[_vm._v("Компания Sika способствует социальному и экономическому развитию во всех странах и регионах своего присутствия, оказывая активную поддержку социальным проектам на местах. Как участник Глобального договора ООН, компания следует принципам глобального устойчивого развития.")]),_c('p',[_vm._v("Компания Sika твердо придерживается принципов честного ведения деятельности и использует жесткие стандарты деловой этики. В Кодексе деловой этики компании Sika установлены стандарты компании и правила поведения всех ее работников.")])])]),_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-textimage__list-bullet"},[_c('div',{staticClass:"cmp-image--left cmp-image"},[_c('picture',{attrs:{"data-image-src":"{\"isResponsive\":true,\"pattern\":\"https://sika.scene7.com/is/image/sika/glo-Sika-Team-Chile-AR2018${imageProfile}?fit\\u003dcrop%2C1\\u0026wid\\u003d${width}\\u0026hei\\u003d${height}\\u0026fmt\\u003d${format}\",\"originalWidth\":3840,\"originalHeight\":2613,\"alt\":\"Employees from Sika Chile\",\"imageProfileNamePrefix\":\":\",\"imageProfileMap\":{}}","data-height-based":"false"}},[_c('source',{attrs:{"type":"image/webp","srcset":"https://sika.scene7.com/is/image/sika/glo-Sika-Team-Chile-AR2018?fit=crop%2C1&wid=480&hei=327&fmt=webp-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/glo-Sika-Team-Chile-AR2018?fit=crop%2C1&wid=600&hei=408&fmt=webp-alpha" + " 1.5x, " + "https://sika.scene7.com/is/image/sika/glo-Sika-Team-Chile-AR2018?fit=crop%2C1&wid=768&hei=523&fmt=webp-alpha" + " 2x"}}),_c('source',{attrs:{"type":"image/png","srcset":"https://sika.scene7.com/is/image/sika/glo-Sika-Team-Chile-AR2018?fit=crop%2C1&wid=480&hei=327&fmt=png-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/glo-Sik