быта. Компания настойчиво реализует свою стратегию и задачи на долгую перспективу.")]),_c('p',[_vm._v("В компании надеются на ответственное отношение каждого сотрудника, при этом им ставятся четко определенные задачи и поручения по проектам. Общую ответственность за прибыль и убытки компании несет Генеральный директор. Компания Sika установила прозрачные критерии оценки финансовых результатов в рамках реализации принятой стратегии. Оценка результатов деятельности осуществляется на основании показателей доли компании на рынке сбыта, роста продаж, доходности и эффективности капитальных вложений."),_c('br')])])])])])])])])])
